import React from "react";
import InternalLink from "components/reusable/atoms/links/InternalLink";

import arrow from "assets/img/link-arrow-gray.svg";
import {
  Container,
  Content,
  IconContainer,
  VideoWrapper,
  Video,
  Title,
} from "./elements";

const VideoLink = ({ title, image, linkPath, videoPath }) => {
  const videoElement = React.createRef();
  return (
    <InternalLink
      to={linkPath}
      style={{
        textDecoration: "none",
        height: "100%",
        width: "100%",
        position: "relative",
      }}
    >
      <VideoWrapper
        onMouseEnter={() => videoElement.current.play()}
        onMouseLeave={() => videoElement.current.pause()}
      >
        <Video
          playsInline
          autoPlay={false}
          loop
          muted
          src={videoPath}
          ref={videoElement}
        />
      </VideoWrapper>
      <Content image={image}>
        <IconContainer>
          <img alt="arrow" src={arrow} />
        </IconContainer>
        <Title>{title}</Title>
      </Content>
    </InternalLink>
  );
};

const VideoLinks = ({ leftLink, rightLink }) => (
  <Container>
    <VideoLink
      title={leftLink.title}
      linkPath={leftLink.path}
      image={leftLink.image}
      videoPath={leftLink.videoPath}
    />
    <VideoLink
      title={rightLink.title}
      linkPath={rightLink.path}
      image={rightLink.image}
      videoPath={rightLink.videoPath}
    />
  </Container>
);

export default VideoLinks;
