import React from "react";
import { Row, Column } from "components/Grid.tsx";

import {
  AlternatingSectionWrapper,
  DetailText,
  DetailVisuals,
  BackgroundImageContainer,
  BackgroundImage,
  HighlightImageContainer,
  HighlightImage,
  Wrapper,
  Logo,
  Partners,
  PartnerWrapper,
  Container,
  UserContainer,
  Testimonial,
  User,
  Bar,
} from "./elements";

const AlternatingSection = ({
  id,
  alternate,
  backgroundImage,
  darkenBackground = true,
  highlightImage,
  children,
  largeDetailText,
  testimonial,
  partners,
  backgroundImageSrcSet = "",
  backgroundImageSizes = "",
}) => (
  <AlternatingSectionWrapper
    id={id}
    alternate={alternate}
    className="section travel--detail"
  >
    <Row
      padding={30}
      gutterWidth={30}
      reversedFlexDirection={alternate}
      verticalAlignment="center"
    >
      <Column span={1} />
      <Column span={6} spanMedium={10} spanSmall={12}>
        <DetailVisuals alternate={alternate}>
          <BackgroundImageContainer fullWidth>
            <BackgroundImage
              src={backgroundImage}
              alt="background image"
              darken={darkenBackground}
              srcSet={backgroundImageSrcSet}
              sizes={backgroundImageSizes}
            />
          </BackgroundImageContainer>
          {highlightImage && (
            <HighlightImageContainer alternate={alternate}>
              <HighlightImage
                alternate={alternate}
                src={highlightImage}
                alt="highlight image"
              />
            </HighlightImageContainer>
          )}
        </DetailVisuals>
      </Column>
      <Column span={1} />
      <Column span={3} spanMedium={10} spanSmall={12}>
        <DetailText alternate={alternate} large={largeDetailText}>
          {children}
        </DetailText>
      </Column>
      <Column span={1} />
    </Row>

    {(partners || testimonial) && (
      <Row
        padding={30}
        gutterWidth={30}
        reversedFlexDirection={!alternate}
        withTopMargin
        verticalAlignment="center"
      >
        {partners ? (
          <Column span={7} spanSmall={12}>
            <Partners>
              {partners.map((partner, i) => (
                <PartnerWrapper key={`partner-wrapper-${i}`}>
                  <Logo
                    alt="partner"
                    src={partner.logo}
                    height={partner.height}
                    width={partner.width}
                  />
                </PartnerWrapper>
              ))}
            </Partners>
          </Column>
        ) : (
          <Column span={7} spanSmall={0} />
        )}
        <Column span={4} spanSmall={12}>
          {testimonial && (
            <Container>
              <Wrapper>
                <Testimonial>{testimonial.quote}</Testimonial>
                <UserContainer>
                  <User>
                    {`${testimonial.user.name},`}
                    <br />
                    {`${testimonial.user.role}`}
                  </User>
                  <Logo
                    alt="testimonial"
                    src={testimonial.company.logo}
                    height={testimonial.company.height}
                    width={testimonial.company.width}
                  />
                </UserContainer>
                <Bar />
              </Wrapper>
            </Container>
          )}
        </Column>
        <Column span={1} />
      </Row>
    )}
  </AlternatingSectionWrapper>
);

export default AlternatingSection;
