import React from "react";
import styled from "@emotion/styled";

import SignUpReferenceLink from "components/reusable/atoms/links/SignUpReferenceLink";
import OutboundLink from "components/reusable/atoms/links/OutboundLink";
import { withClickAnalytics } from "hocs/analytics";
import CompanyTypes from "utils/companyTypes";
import { Row, Column } from "components/Grid.tsx";
import SimpleValueProp, { sizes } from "components/SimpleValueProp";

import calendar from "assets/img/ecommerce/60-day-calendar.svg";
import noInterest from "assets/img/ecommerce/no-interest.svg";
import instantApproval from "assets/img/ecommerce/instant-approval.svg";

import {
  Container,
  ContentWrapper,
  Content,
  Primary,
  ButtonsContainer,
  LearnButton,
} from "./elements";

const ApplyButton = styled(
  withClickAnalytics(
    "Button",
    "ecomLP.simpleValueProp.signUpButtonClicked",
  )(SignUpReferenceLink),
)({
  fontSize: 14,
});

const valueProps = [
  {
    icon: calendar,
    title: "Net 60 terms",
    caption: (
      <Content>
        Take a full 60 days to pay to better manage your cash flow.{" "}
        {
          // hash links need to use anchor tags because Gatsby
          // doesn't support them well
        }
        <OutboundLink href="#how-it-works">
          <Primary>See how it works.</Primary>
        </OutboundLink>
      </Content>
    ),
  },
  {
    icon: noInterest,
    title: "Interest-free",
    caption: <Content>No interest, no fees, no catch.</Content>,
  },
  {
    icon: instantApproval,
    title: "Instant approvals",
    caption: (
      <Content>
        Apply in as little as 5 minutes, and get your virtual card instantly
        upon approval.{" "}
        <OutboundLink href="#faq">
          <Primary>See how to get started.</Primary>
        </OutboundLink>
      </Content>
    ),
  },
];

const SimpleValueProps = ({ includeLearnMore = true }) => (
  <Container>
    <Row padding={30} gutterWidth={26}>
      <Column span={1} />
      <Column span={10}>
        <ContentWrapper>
          {valueProps.map((prop) => (
            <SimpleValueProp
              icon={prop.icon}
              title={prop.title}
              content={prop.caption}
              size={sizes.large}
              key={prop.title}
            />
          ))}
        </ContentWrapper>
      </Column>
      <Column span={1} />
    </Row>
    <ButtonsContainer>
      <ApplyButton
        className="btn btn-cta btn-primary text-white mb-3 mr-3 ml-3"
        role="button"
        companyType={CompanyTypes.STARTUP}
      >
        SIGN UP
      </ApplyButton>
      {includeLearnMore && (
        <LearnButton href="#howItWorks" className="btn mb-3 ml-sm-3 mr-3 ml-3">
          Learn More
        </LearnButton>
      )}
    </ButtonsContainer>
  </Container>
);

export default SimpleValueProps;
