import React from "react";

import AlternatingSection from "components/ecommerce/AlternatingSection";
import InternaLinkPrimaryButton from "components/reusable/atoms/buttons/InternalLinkPrimaryButton";
import { withClickAnalytics } from "hocs/analytics";
import InternalLink from "components/reusable/atoms/links/InternalLink";

import { Title, Subtitle, Content, Spacer, ButtonWrapper } from "./elements";

const SideBySide = ({
  id,
  alternate,
  messages,
  backgroundImage,
  highlightImage,
  backgroundImageSrcSet = "",
  backgroundImageSizes = "",
}) => (
  <React.Fragment>
    <AlternatingSection
      id={id}
      alternate={alternate}
      className="section"
      backgroundImage={backgroundImage}
      highlightImage={highlightImage}
      darkenBackground={false}
      testimonial={messages.testimonial}
      partners={messages.partners}
      largeDetailText
      backgroundImageSrcSet={backgroundImageSrcSet}
      backgroundImageSizes={backgroundImageSizes}
    >
      <Title>{messages.title}</Title>
      <Content>{messages.subtitle}</Content>
      {messages.sections.map((section) => (
        <div key={section.title}>
          <Subtitle>{section.title}</Subtitle>
          {section.description.map((span) => {
            const { link } = span;
            if (link) {
              const AnalyticsLink = withClickAnalytics(
                "Link",
                link.event,
              )(InternalLink);
              return (
                <Content key={span.link.to}>
                  <AnalyticsLink to={span.link.to}>
                    {span.content}
                  </AnalyticsLink>
                </Content>
              );
            }
            return <Content key={span.content}>{span.content}</Content>;
          })}
        </div>
      ))}
      {messages.button && (
        <ButtonWrapper>
          <InternaLinkPrimaryButton to={messages.button.link}>
            {messages.button.caption}
          </InternaLinkPrimaryButton>
        </ButtonWrapper>
      )}
    </AlternatingSection>
    <Spacer />
  </React.Fragment>
);

export default SideBySide;
